.contacts__title {
  @include title(18px, 1.67);
  align-self: flex-start;
}

.contacts__wrapper {
  @include wrapper(320px, 30px);

  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 37px auto;
}

.contacts__text {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: flex-start;
  margin: 0;
  margin-top: 26px;

  line-height: 2;
}

.contacts__item {
  &:first-child {
    margin-right: 25px;
  }

  &--link {
    border-bottom: 1px solid $green;
  }

  &--link:hover {
    border-bottom-color: #46c1ae;
    color: $green;
  }

  &--link:active {
    opacity: 0.3;
  }
}

.contacts__map {
  display: block;
  margin: 0 auto;
  width: 320px;
  height: 457px;

  font-size: 0;

  background-color: #f4e9d7;
  background-image: url("../img/map-mobile@1x.jpg");

  @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/map-mobile@2x.jpg");
  }
  background-size: 320px 457px;
}

.contacts__frame {
  border: none;

  &--mobile {
    display: block;
  }

  &--tablet {
    display: none;
  }

  &--desktop {
    display: none;
  }
}

.contacts__button {
  margin: 55px auto;

  width: 260px;
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    @include wrapper($tablet-width, 50px);
    padding-top: 85px;
    margin-bottom: 55px;
  }

  .contacts__title {
    @include title(43px, 1.16);
    position: relative;
    width: 100%;

    &::before {
      content: "";

      position: absolute;
      top: 0;
      right: 0;

      margin-top: -10px;
      width: 47px;
      height: 81px;

      background: url("../img/icon-flag.svg");
      background-repeat: no-repeat;
    }
  }

  .contacts__info {
    display: flex;
    justify-content: flex-start;
    width: 100%;
  }

  .contacts__text {
    margin-top: 50px;
    width: 50%;

    font-size: 17px;
    font-weight: normal;
    line-height: 1.76;
    text-align: left;
    color: $brown;
  }

  .contacts__map {
    width: $tablet-width;
    height: 457px;

    background-image: url("../img/map-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/map-tablet@2x.jpg");
    }
    background-size: $tablet-width 457px;
  }

  .contacts__frame {
    &--mobile {
      display: none;
    }

    &--tablet {
      display: block;
    }
  }

  .contacts__button {
    width: 230px;
  }
}

@media (min-width: $desktop-width) {
  .contacts__container {
    @include wrapper($desktop-width, 50px);
    padding-left: calc(50px + 175px);
    position: relative;
    margin: 0 auto;
    margin-bottom: 110px;
  }

  .contacts__wrapper {
    align-items: flex-start;
    width: auto;
    margin: 0;
    padding: 0;
    margin-top: 150px;
    padding-right: 50px;
  }

  .contacts__title {
    @include title(53px, 1.08);
    margin-top: 80px;

    &::before {
      left: 0;
      margin-left: -115px;
    }
  }

  .contacts__info {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 0;
  }

  .contacts__text {
    width: 100%;
    margin-top: 40px;
  }

  .contacts__map {
    position: absolute;
    top: 0;
    right: 50px;

    width: 525px;

    background-image: url("../img/map-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/map-desktop@2x.jpg");
    }
    background-size: 525px 457px;
  }

  .contacts__frame {
    &--tablet {
      display: none;
    }

    &--desktop {
      display: block;
    }
  }

  .contacts__button {
    width: 230px;
    margin: 0;
    margin-top: 68px;
  }
}
